import React, {useState} from 'react'
import {Group} from "@vkontakte/vkui"
import CardGrid from "@vkontakte/vkui/dist/components/CardGrid/CardGrid"
import Card from "@vkontakte/vkui/dist/components/Card/Card"

import clock from "../../img/mainPanel/clock.png"
import money from "../../img/mainPanel/money.png"
import "./HomePanel.css"
import AcceleratorsModal from "../homeModals/AcceleratorsModal";

const HomePanel = () => {
  const [openAccelerators, setOpenAccelerators] = useState(false)
  const [openMoney, setOpenMoney] = useState(false)
  const [showModalA, setShowModalA] = useState(showModalA)

  return openAccelerators ? (
    <AcceleratorsModal/>
  ) : (
    <Group>
      <CardGrid>
        <Card size="s">
          <div className="CardDiv">
            <img
              src={clock}
              alt="clock"
              className="CardImg"
              onClick={() => {
                setOpenAccelerators(true)
              }}
            />
          </div>
        </Card>
        <Card size="s">
          <div className="CardDiv">
            <img
              src={money}
              alt="clock"
              className="CardImg"
            />
          </div>
        </Card>
        <Card size="s">
          <div className="CardDiv"/>
        </Card>
      </CardGrid>
    </Group>
  )
}

export default HomePanel