import React, {useState} from 'react'
import PropTypes from 'prop-types'
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel'
import Group from '@vkontakte/vkui/dist/components/Group/Group'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar'
import HomePanel from '../components/homePanel/HomePanel'

import coin from '../img/clickButton/coin.png'

const Home = ({id, fetchedUser}) => {
  const [counter, setCounter] = useState(0.000)
  const [imgHeight, setImgHeight] = useState('300px')

  return (
    <Panel id={id}>
      {fetchedUser &&
      <PanelHeader
        left={
          <Cell
            before={fetchedUser.photo_100 && (
              <Avatar src={fetchedUser.photo_100}/>
            )}
            description={fetchedUser.first_name}
          />
        }
      >
        Clicker
      </PanelHeader>
      }
      <Div style={{textAlign: 'center'}}>
        <h1>–£ –≤–∞—Å {counter} <span role="img" aria-label="diamond">üíé</span></h1>
      </Div>
      <HomePanel/>
      <Group>
        <Div>
          <img
            src={coin}
            style={{display: 'block', margin: '0 auto', transition: '.1s'}}
            alt="coin"
            height={imgHeight}
            onClick={() => {
              setCounter(parseFloat(Number(counter) + 0.001).toFixed(3))

              setImgHeight('250px')
              setTimeout(() => {
                setImgHeight('300px')
              }, 100)
            }}
          />
        </Div>
      </Group>
    </Panel>
  )
}

Home.propTypes = {
  id: PropTypes.string.isRequired,
  fetchedUser: PropTypes.shape({
    photo_100: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
}

export default Home
